import { BrowserModule } from "@angular/platform-browser";
import { ErrorHandler, NgModule } from "@angular/core";
import { IonicApp, IonicErrorHandler, IonicModule } from "ionic-angular";
import { StatusBar } from "@ionic-native/status-bar";
import { SplashScreen } from "@ionic-native/splash-screen";
import { AngularFireModule } from "angularfire2";
import { AngularFireAuthModule } from 'angularfire2/auth';
import { AngularFirestoreModule } from "angularfire2/firestore";
import { AngularFireStorageModule } from 'angularfire2/storage';
import { environment } from "../environments/environment";
import { FormsModule } from "@angular/forms";
import { Camera } from "@ionic-native/camera";
import { UploadFileService } from '../providers/providers';
import { ProductService } from '../providers/providers';
import { MyApp } from "./app.component";

import { PipesModule } from "../pipes/pipes.module";
import { ComponentsModule } from '../components/components.module';

import { HomePageModule } from "../pages/home/home.module";
import { AddItemPageModule } from "../pages/add-item/add-item.module";
import { AddCategoryPageModule } from "../pages/add-category/add-category.module";
import { PopoverPageModule } from "../pages/popover/popover.module";
import { ProductsPageModule } from "../pages/products/products.module";
import { ProductDetailPageModule } from "../pages/product-detail/product-detail.module";
import { TabsPageModule } from "../pages/tabs/tabs.module";
import { SearchPageModule } from "../pages/search/search.module";
import { CartPageModule } from "../pages/cart/cart.module";
import { LocatorPageModule } from "../pages/locator/locator.module";
import { LoginPageModule } from "../pages/login/login.module";
import { SignUpPageModule } from "../pages/sign-up/sign-up.module";
import { AuthService } from '../providers/providers';




@NgModule({
  declarations: [
    MyApp,
  ],
  imports: [
    BrowserModule,   
    FormsModule,
    PipesModule,
    ComponentsModule,
    AngularFireModule.initializeApp(environment.firebaseConfig),
    AngularFirestoreModule,
    AngularFireStorageModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/add-category/add-category.module.ngfactory#AddCategoryPageModuleNgFactory', name: 'AddCategoryPage', segment: 'add-category', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/add-item/add-item.module.ngfactory#AddItemPageModuleNgFactory', name: 'AddItemPage', segment: 'add-item', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/cart/cart.module.ngfactory#CartPageModuleNgFactory', name: 'CartPage', segment: 'cart', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/home.module.ngfactory#HomePageModuleNgFactory', name: 'HomePage', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/locator/locator.module.ngfactory#LocatorPageModuleNgFactory', name: 'LocatorPage', segment: 'locator', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module.ngfactory#LoginPageModuleNgFactory', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/popover/popover.module.ngfactory#PopoverPageModuleNgFactory', name: 'PopoverPage', segment: 'popover', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/product-detail/product-detail.module.ngfactory#ProductDetailPageModuleNgFactory', name: 'ProductDetailPage', segment: 'product-detail', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/products/products.module.ngfactory#ProductsPageModuleNgFactory', name: 'ProductsPage', segment: 'products', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/search/search.module.ngfactory#SearchPageModuleNgFactory', name: 'SearchPage', segment: 'search', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/sign-up/sign-up.module.ngfactory#SignUpPageModuleNgFactory', name: 'SignUpPage', segment: 'sign-up', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/tabs/tabs.module.ngfactory#TabsPageModuleNgFactory', name: 'TabsPage', segment: 'tabs', priority: 'low', defaultHistory: [] }
  ]
}),
    HomePageModule,
    ProductsPageModule,
    AddCategoryPageModule,
    PopoverPageModule,
    ProductDetailPageModule,
    AddItemPageModule,
    TabsPageModule,
    SearchPageModule,
    CartPageModule,
    LoginPageModule,
    SignUpPageModule,
    SearchPageModule,
    LocatorPageModule,
    AngularFireAuthModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp
  ],
  providers: [
    StatusBar,
    Camera,
    SplashScreen,
    { provide: ErrorHandler, useClass: IonicErrorHandler },
    UploadFileService,
    ProductService,
    AuthService
  ]
})
export class AppModule { }



