import { Injectable } from "@angular/core";
import * as firebase from "firebase/app";

@Injectable()
export class AuthService {
  private auth: any;
  constructor() {
    this.auth = firebase.auth();
  }

  signIn(email: string, password: string): Promise<any> {
    return this.auth
      .signInWithEmailAndPassword(email, password)
      .catch(() => {
    });
  }

  registerUser(email: string, password: string) {
    return this.auth
      .createUserWithEmailAndPassword(email, password)
      .then(() => {
      })
      .catch(() => {
    });
  }

  googleSignIn() {
    this.auth.languageCode = 'pt';
    var provider = new firebase.auth.GoogleAuthProvider();
    provider.addScope('https://www.googleapis.com/auth/contacts.readonly');
    provider.setCustomParameters({
      'login_hint': 'user@example.com'
    });
    return this.auth.signInWithPopup(provider)
      .then((result) => {
        console.log('Sucess', JSON.stringify(result));
        //var token = result.credential.accessToken;
        //var user = result.user;
      }).catch((error) => {
        console.log('Failed', JSON.stringify(error));
        //var errorCode = error.code;
        //var errorMessage = error.message;
        var email = error.email;
        //var credential = error.credential;
        // An error happened.
        if (error.code === 'auth/account-exists-with-different-credential') {
          var pendingCred = error.credential;
          email = error.email;
          this.auth.fetchSignInMethodsForEmail(email).then(function (methods) {
            if (methods[0] === 'password') {
              var password = this.promptUserForPassword();
              this.auth.signInWithEmailAndPassword(email, password).then(function (user) {
                return user.link(pendingCred);
              }).then(function () {
                // Google account successfully linked to the existing Firebase user.
                this.goToApp();
              });
              return;
            }
            // All the other cases are external providers.
            // Construct provider object for that provider.
            // TODO: implement getProviderForProviderId.
            var provider = this.getProviderForProviderId(methods[0]);
            // At this point, you should let the user know that he already has an account
            // but with a different provider, and let him validate the fact he wants to
            // sign in with this provider.
            // Sign in to provider. Note: browsers usually block popup triggered asynchronously,
            // so in real scenario you should ask the user to click on a "continue" button
            // that will trigger the signInWithPopup.
            this.auth.signInWithPopup(provider).then(function (result) {
              // Remember that the user may have signed in with an account that has a different email
              // address than the first one. This can happen as Firebase doesn't control the provider's
              // sign in flow and the user is free to login using whichever account he owns.
              // Step 4b.
              // Link to Google credential.
              // As we have access to the pending credential, we can directly call the link method.
              result.user.linkAndRetrieveDataWithCredential(pendingCred).then(function (usercred) {
                // Google account successfully linked to the existing Firebase user.
                this.goToApp();
              });
            });
          });
        }
      });
  }

  facebookSignIn() {
    var provider = new firebase.auth.FacebookAuthProvider();
    provider.addScope('user_birthday');
    this.auth.languageCode = 'fr_FR';
    provider.setCustomParameters({
      'display': 'popup'
    });
    return this.auth.signInWithRedirect(provider)(provider).then((result) => {
      console.log('Sucess', JSON.stringify(result));
      //var token = result.credential.accessToken;
      //var user = result.user;
    }).catch((error) => {
      console.log('Facebook Faiure', JSON.stringify(error));
      //var errorCode = error.code;
      //var errorMessage = error.message;
      //var email = error.email;
      //var credential = error.credential;
    });
  }

  goToApp() {

  }

  promptUserForPassword() {

  }

  getProviderForProviderId() {

  }

  resetPassword(email: string): Promise<any> {
    return this.auth.sendPasswordResetEmail(email);
  }

  signOut(): void {
    firebase
      .auth()
      .signOut()
      .then(() => {
      })
      .catch(() => {
      });
  }
}



