import { Component } from "@angular/core";
import { NavController, ToastController, MenuController } from "ionic-angular";
import { AuthService } from "../../providers/providers";
import { User } from "../../models/User";


@Component({
  selector: "page-sign-up",
  templateUrl: "sign-up.html"
})
export class SignUpPage {
  public logo = "assets/imgs/logo.png";
  user = new User();
  constructor(
    public navCtrl: NavController,
    public authService: AuthService,
    public toast: ToastController,
    public menu: MenuController
  ) { }
  ionViewWillEnter() {
    this.menu.enable(false);
  }
  ionViewWillLeave() {
    this.menu.enable(false);
  }

  signUp(user: User): void {
    this.authService
      .registerUser(user.email, user.password)
      .then(res => {
        this.presentMessage(true, res);
        this.navCtrl.popTo("LoginPage");
      })
      .catch(error => {
        this.presentMessage(false, error);
      });
  }

  login() {
    this.navCtrl.pop();
  }

  presentMessage(isSuccess: boolean, message: any) {
    let opt = null;
    if (isSuccess) {
      opt = {
        message: "You have successfully registered for an account",
        duration: 3000,
        position: 'bottom'
      }
    } else {
      opt = {
        message: message.message,
        duration: 3000,
        position: 'bottom'
      }
    }
    this.toast.create(opt).present();
  }
}



