import { Component } from '@angular/core';
import { ViewController, ModalController, NavController, AlertController, NavParams } from 'ionic-angular';

@Component({
  selector: 'page-popover',
  template: `
  <ion-list>
    <button ion-item (click)="openMemberDetails()">Details</button>
    <button ion-item (click)="showCheckin()">Add Product</button>    
    <button ion-item (click)="showConfirm()">Delete</button>
  </ion-list>
`
})

export class PopoverPage {

  constructor(public viewCtrl: ViewController, private alertCtrl: AlertController, public modalCtrl: ModalController, public navParams: NavParams, public navCtrl: NavController) { }

  close() {
    this.viewCtrl.dismiss();
  }

  openMemberDetails(member: any) {
    this.navCtrl.push('', {
      member: member
    });
  }

  showCheckin(member: any) {
    let modal = this.modalCtrl.create('', { member: member });
    modal.onDidDismiss((item) => {

    });
    modal.present();
  }

  showConfirm(item) {
    let confirm = this.alertCtrl.create({
      title: 'Delete',
      message: 'Are you sure you want to Delete this record?',
      buttons: [
        {
          text: 'No',
          handler: () => {
          }
        },
        {
          text: 'Yes',
          handler: () => {
            this.removeMember(item);
          }
        }
      ]
    });
    confirm.present();
  }

  removeMember(item) {
    // this.membersRef.remove(item.name).then((res) => {
    //   let alert = this.alertCtrl.create({
    //     title: 'Delete Member',
    //     message: 'Member Removed Successfully!' + res,
    //     buttons: ['Ok'],
    //   });
    //   alert.present();
    // }).catch((error) => {
    //   let alert = this.alertCtrl.create({
    //     title: 'Delete Member',
    //     message: 'Member Removed Successfully!' + error,
    //     buttons: ['Ok'],
    //   });
    //   alert.present();
    // });
  }

}



