import { Component, ViewChild, OnInit } from "@angular/core";
import { UploadFileService } from "../../providers/upload-file-service/upload-file-service";
import { NavController, ViewController, NavParams, ToastController } from "ionic-angular";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { Camera } from "@ionic-native/camera";
import { FileUpload } from "../../models/FileUpload";
import { Category } from "../../models/Category";
import { ProductService } from "../../providers/providers";

@Component({
  selector: "page-add-item",
  templateUrl: "add-item.html"
})
export class AddItemPage implements OnInit {
  @ViewChild("fileInput") fileInput;
  public options: Category[];
  category: Category;
  public fileUrl: string;
  isReadyToSave: boolean;
  public isprofilePicSet = false;
  selectedFiles: FileList;
  currentFileUpload: FileUpload;
  progress: { percentage: number } = { percentage: 0 };
  productForm: FormGroup;

  constructor(
    public nCtrl: NavController,
    public tCtrl: ToastController,
    public navParams: NavParams,
    public uploadService: UploadFileService,
    public service: ProductService,
    public vCtrl: ViewController,
    public fb: FormBuilder,
    public camera: Camera
  ) {
    this.category = this.navParams.get("category");           
  }

  ngOnInit() { 
    this.createForm();
    this.getCategories();    
  }

  ionViewDidLoad(){
    if(this.category.categoryName != null){      
      this.productForm.controls["category"].setValue(this.category.categoryName);
    }
  }
  getCategories(): void {
    this.service.getCategories().subscribe(categories => {
      this.options = categories;
    });
  }

  createForm(): void {
    this.productForm = this.fb.group({
      profilePic: [""],
      productName: ["", Validators.required],
      storeName: ["", Validators.compose([Validators.required, Validators.minLength(3)])],
      storePrice: ["", Validators.required],
      category: [""],
      productDescription: [""]
    });
    this.subscribeFormChanges();
  }

  subscribeFormChanges() {
    this.productForm.valueChanges.subscribe(v => {
      this.isReadyToSave = this.productForm.valid;
    });
  }

  rebuildForm() {
    this.productForm.reset(); 
  }

  getPicture() {
    if (Camera["installed"]()) {
      this.camera
        .getPicture({
          destinationType: this.camera.DestinationType.DATA_URL,
          targetWidth: 50,
          targetHeight: 50
        })
        .then(
          data => {
            this.productForm.patchValue({
              profilePic: "data:image/jpg;base64," + data
            });
          },
          err => {
            alert("Unable to take photo "+ err);
          }
        );
    } else {
      this.fileInput.nativeElement.click();
    }
  }

  processWebImage(event) {
    let reader = new FileReader();
    reader.onload = readerEvent => {
      let imageData = (readerEvent.target as any).result;
      this.productForm.patchValue({ profilePic: imageData });
    };
    this.isprofilePicSet = true;
    reader.readAsDataURL(event.target.files[0]);
    this.selectedFiles = event.target.files;
    const file = this.selectedFiles.item(0);
    this.currentFileUpload = new FileUpload(file);
    this.uploadService.pushFileToStorage(this.currentFileUpload, this.progress)
      .subscribe((res) => {       
        this.productForm.controls["profilePic"].setValue(res);
      });
  }

  getProfileImageStyle() {
    return "url(" + this.productForm.controls["profilePic"].value + ")";
  }

  cancel() {
    this.vCtrl.dismiss();
  }

  createItem() {
    this.saveProduct();
  }

  done() {
    this.saveProduct();
  }

  saveProduct() {
    if (!this.productForm.valid) {
      return;
    }
    var res = this.service.saveProduct(this.productForm.value);
    if (res) {      
      this.rebuildForm();
    } else { }
  }
}



